<template>
    <template v-for="(field, index) in form.fields" :key="field.name">
        <div :class="{'form-input-margin': index !== 0 && field.widget.name !== 'SelectOther'}" v-if="checkVisible(field)">
            <template v-if="field.widget.name === 'TextInput'">
                <div class="input-text">
                    <p>{{ field.label }}</p>
                    <small>{{ field.help_text }}</small>
                </div>
                <input class="textbox" type="text" :name="field.name" :id="field.name" v-model="answers[field.name]" v-bind="field.widget.attrs">
            </template>
            <template v-else-if="field.widget.name === 'Textarea'">
                <div class="input-text">
                    <p>{{ field.label }}</p>
                    <small>{{ field.help_text }}</small>
                </div>
                <textarea class="textbox" :name="field.name" :id="field.name" v-model="answers[field.name]" v-bind="field.widget.attrs"></textarea>
            </template>
            <template v-else-if="field.widget.name === 'SelectOther'">
                <input type="checkbox" :id="field.name+'-checkbox'" v-model="answers[field.name+'-checkbox']">
                <label :for="field.name+'-checkbox'">Annat: </label>
                <input class="select-other textbox" type="text" :name="field.name" :id="field.name" v-model="answers[field.name]" v-bind="field.widget.attrs" :disabled="!answers[field.name+'-checkbox']">
            </template>
            <template v-else-if="field.widget.name === 'RadioYesNo'">
                <div class="input-text">
                    <p>{{ field.label }}</p>
                    <small>{{ field.help_text }}</small>
                </div>
                <label class="checkbox-label" :for="field.name+'-yes'"><input type="radio" :name="field.name" value="true" :id="field.name+'-yes'" v-model="answers[field.name]">Ja</label>
                <label class="checkbox-label" :for="field.name+'-no'"><input type="radio" :name="field.name" value="false" :id="field.name+'-no'" v-model="answers[field.name]">Nej</label>
            </template>
            <template v-else-if="field.widget.name === 'RadioSelect'">
                <div class="input-text">
                    <p>{{ field.label }}</p>
                    <small>{{ field.help_text }}</small>
                </div>
                <template v-for="choice in field.widget.choices" :key="choice[0]">
                    <label class="checkbox-label" :for="field.name+choice[0]"><input type="radio" :name="choice[0]" :value="choice[0]" :id="field.name+choice[0]" v-model="answers[field.name]"> {{ choice[1] }}</label>
                </template>
            </template>
            <template v-else-if="field.widget.name === 'CheckboxSelectMultiple'">
                <div class="input-text">
                    <p>{{ field.label }}</p>
                    <small>{{ field.help_text }}</small>
                </div>
                <template v-for="choice in field.widget.choices" :key="choice[0]">
                    <label class="checkbox-label" :for="field.name+choice[0]"><input type="checkbox" :name="choice[0]" :value="choice[0]" :id="field.name+choice[0]" v-model="answers[field.name]"> {{ choice[1] }}</label>
                </template>
            </template>
            <template v-else>
                widget not implemented
                {{ field }}
            </template>
            <template v-for="error in field.errors" :key="error">
                <p class="error">{{ error }}</p>
            </template>
        </div>
    </template>
    <input v-if="form.fields" class="button" type="button" @click="handleSubmit" :value="button_text">
    <template v-for="error in form.non_field_errors" :key="error">
        <p class="error">{{ error }}</p>
    </template>
</template>

<script>
export default {
    props: { form: { default: [] }, button_text: { default: 'Skicka in' } },
    emits: ['submit'],
    data () {
        return {
            answers: {},
        }
    },
    methods: {
        handleSubmit () {
            this.$emit('submit', this.cleanedAnswers)
        },
        checkVisible (field) {
            return !(field.requires && this.cleanedAnswers[field.requires[0]] !== field.requires[1])
        },
        init () {
            for (const i in this.form.fields) {
                switch (this.form.fields[i].widget.name) {
                case 'CheckboxSelectMultiple':
                    this.answers[this.form.fields[i].name] = []
                    break
                }
            }
        },
        fillInits () {
            for (const i in this.form.fields) {
                if (this.form.fields[i].initial) {
                    this.answers[this.form.fields[i].name] = this.form.fields[i].initial
                }
            }
        },
    },
    computed: {
        cleanedAnswers () {
            const obj = {}

            for (const i in this.form.fields) {
                const field = this.form.fields[i]
                switch (field.widget.name) {
                case 'TextInput':
                    obj[field.name] = this.answers[field.name] || ''
                    break
                case 'Textarea':
                    obj[field.name] = this.answers[field.name] || ''
                    break
                case 'SelectOther':
                    obj[field.name] = (this.answers[field.name + '-checkbox']) ? this.answers[field.name] || '' : ''
                    break
                case 'RadioYesNo':
                    if (this.answers[field.name] === undefined) {
                        obj[field.name] = null
                        break
                    }
                    obj[field.name] = this.answers[field.name] === 'true'
                    break
                case 'RadioSelect':
                    if (this.answers[field.name] === undefined) {
                        obj[field.name] = null
                        break
                    }
                    obj[field.name] = this.answers[field.name]
                    break
                case 'CheckboxSelectMultiple':
                    obj[field.name] = this.answers[field.name]
                    break
                default:
                    console.error('unknown widget ' + field.widget.name)
                }
            }

            return obj
        },
    },
    watch: {
        'form' (newForm, oldForm) {
            if (oldForm.fields === []) {
                this.init()
            }
            this.fillInits()
        },
    },
    mounted () {
        this.init()
        this.fillInits()
    },
}
</script>

<style lang="scss" scoped>
.input-text {
    margin-bottom: 0.5rem;
    p {
        margin-bottom: 0rem;
    }
}

.form-input-margin {
    margin-top: 1.5rem;
}

.checkbox-label {
    display: block;
}

.button {
    margin: 1rem 0;
}

.error {
    color: rgb(190, 30, 30);
    margin: 0;
}
</style>
