<template>
<h1>Profile</h1>
    <template v-if="signedIn">
        <div class="content">
            <div>
                <h3>Team</h3>
                <template v-if="!$store.state.team">
                    <h5>Skapa lag</h5>
                    <RemoteForm :form="createForm || $store.state.forms.create_team" button_text="Skapa lag" @submit="createTeam"/>
                    <h5>G med i lag</h5>
                    <RemoteForm :form="joinForm || $store.state.forms.join_team" button_text="G med i lag" @submit="joinTeam"/>
                </template>
                <template v-else>
                    <h4>{{ $store.state.team.name }}</h4>
                    <h5 class="eligible" v-if="$store.state.team.eligible">Inom tvlan</h5>
                    <h5 class="eligible" v-else>Utom tvlan</h5>
                    <RemoteForm :form="editForm || $store.state.forms.edit_team" button_text="Redigera lag" @submit="editTeam"/>
                    <h5>Join token</h5>
                    <code>{{ $store.state.team.join_token }}</code>
                    <p>Skicka denna till alla du vill ska g med i ditt lag.</p>
                </template>
            </div>
            <div>
                <h3>Link account</h3>
                <div class="sso-area">
                    <div class="sso-container">
                        <template v-for="provider in $store.state.oauthProviders" :key="provider">
                            <OauthButton v-if="provider" :provider="provider"/>
                        </template>
                    </div>
                </div>
            </div>
        </div>
        <div class="center">
            <button class="button signout-button" @click="signOut">Sign Out</button>
        </div>
    </template>
    <div class="center" v-else>
        <h3>Not logged in</h3>
    </div>
</template>

<script>
import OauthButton from '@/components/OauthButton.vue'
import RemoteForm from '@/components/RemoteForm.vue'
import networking from '@/networking.js'

export default {
    components: {
        OauthButton,
        RemoteForm,
    },
    data () {
        return {
            createForm: null,
            joinForm: null,
            editForm: null,
        }
    },
    methods: {
        async createTeam (event) {
            const r = await networking.request('POST', '/api/team', event)
            if (r.status !== 200) {
                this.$store.commit('createToast', {
                    type: 'error',
                    title: `Backend could not be reached: ${r.status}`,
                    body: 'Please try again.',
                })
                return
            }
            const data = await r.json()
            this.createForm = data.form
            if (data.success) {
                this.$store.commit('updateUserTeam', data.team)
                this.$store.commit('createToast', {
                    type: 'success',
                    title: 'Team successfully created!',
                })
            }
        },
        async joinTeam (event) {
            const r = await networking.request('POST', '/api/team/join', event)
            if (r.status !== 200) {
                this.$store.commit('createToast', {
                    type: 'error',
                    title: `Backend could not be reached: ${r.status}`,
                    body: 'Please try again.',
                })
                return
            }
            const data = await r.json()
            this.joinForm = data.form
            if (data.success) {
                this.$store.commit('updateUserTeam', data.team)
                for (const challenge in data.challenges) {
                    this.$store.commit('processChallenge', data.challenges[challenge])
                }
                this.$store.commit('createToast', {
                    type: 'success',
                    title: `Successfully joined team ${data.team.name}!`,
                })
            }
        },
        async editTeam (event) {
            const r = await networking.request('PATCH', '/api/team', event)
            if (r.status !== 200) {
                this.$store.commit('createToast', {
                    type: 'error',
                    title: `Backend could not be reached: ${r.status}`,
                    body: 'Please try again.',
                })
                return
            }
            const data = await r.json()
            this.editForm = data.form
            if (data.success) {
                this.$store.commit('updateUserTeam', data.team)
                this.$store.commit('createToast', {
                    type: 'success',
                    title: 'Team successfully updated!',
                })
            }
        },
        async signOut () {
            const r = await networking.request('POST', '/api/logout')
            if (r.status !== 200) {
                this.$store.commit('createToast', {
                    type: 'error',
                    title: `Backend could not be reached: ${r.status}`,
                    body: 'Please try again.',
                })
                return
            }
            const data = await r.json()
            if (data.success) {
                this.$store.commit('logout')
                this.$router.replace({ name: 'home' })
                this.$store.commit('createToast', {
                    type: 'success',
                    title: 'Signed out',
                })
            }
        },
    },
    computed: {
        signedIn () {
            return !!this.$store.state.user
        },
    },
}
</script>

<style lang="scss" scoped>
* {
    text-align: center;
}

p {
    margin: 0;
}

.eligible {
    color: #ffffff
}

.content {
    display: grid;
    grid-template-columns: 50% 50%;
    padding-top: 3rem;
}

@media only screen and (max-width: 990px) {
    .content {
        display: block;
    }
}

.sso-area {
  text-align: center;
}

.sso-container {
  display: inline-block;
}

.signout-button {
    margin-top: 1rem;
}
</style>
